@import url('https://fonts.googleapis.com/css2?family=Oleo+Script:wght@700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100;0,200;0,300;0,400;0,500;1,100;1,300;1,400;1,500&display=swap');

// INDEX
*{
  box-sizing: border-box;
}
html{
  height:-webkit-fill-available;
}
body{
  font-family: 'Raleway', 'Times', 'sans-serif';
  font-weight: 500;
  background: linear-gradient(45deg, rgb(204, 37, 14) 0%,  rgb(214, 99, 5) 50%, rgb(100, 30, 2) 100%);
  background-position: 100% 100%;
  background-repeat: no-repeat;
  color:#fff;
}

header{
  display:flex;
  justify-content: space-between;
  align-items: center;
  color:#fff;
  background-color: rgba(0, 0, 0, .35);
  h1{
    font-family: 'Oleo Script',  'Lucida', 'cursive';
    margin-bottom: 0;
    font-size: 1.5rem;
    margin: 1rem;
    text-align: center;
    &:hover{
      cursor: pointer;
    }
  }
  p{
    margin: 1rem;
    font-weight: 400;
    min-width: 160px;
  }
}

// HOME
div.home{
  max-width: 60ch;
  margin:0 auto;
  text-align: center;
  margin-top: 5rem;
  h2.homeT{
    font-family: 'Oleo Script',  'Lucida', 'cursive';
    font-size: 3rem;
    margin-bottom: .5rem;
  }
  h3{
    margin:0;
  }
  div.cta{
    margin-top: 2rem;
    button{
      border: 1px solid #fff;
      border-radius: 20px;
      padding: .5rem 1rem;
      color: #fff;
      min-width: 80px;
      background-color: transparent;
      margin: auto .5rem;
      font-weight: bolder;
      min-width: 150px;
      &:hover{
        background-color: rgba($color: #fff, $alpha: .3);
        transition: all .25s ease-in-out;
        cursor: pointer;
      }
    }
  }
  p.shortP{
    max-width: 50ch;
    margin:2rem auto;
  }
}

// LOGIN
div.login{
  max-width: 40ch;
  margin:2rem auto;
  text-align: center;
  padding: 2rem;
  background-color: rgba(255, 255, 255, .1);
  border-radius: 20px;
  h2{
    margin-top: 0;
  }
  form{
    display:flex;
    flex-direction: column;
    width:25ch;
    margin:0 auto;
    input{
      margin:.5rem 0;
      padding: .25rem;
      border-radius: 10px;
      border:none;
      text-align: center;
      background-color: rgba(255, 255, 255, .75);
    }
    input[type="submit"]{
      min-width: 15ch;
      margin: 0 auto;
      margin-top: .5rem;
      background-color: #e06219;
      color:#fff;
      font-weight: bolder;
      border: 1px solid #fff;
      &:hover{
        background-color: #fff;
        color: #e06219;
        cursor: pointer;
      }
    }
  }
  p.signup{
    font-size: .8rem;
  }
  button{
    border: 1px solid #fff;
    border-radius: 20px;
    padding: .25rem .5rem;
    color: #fff;
    min-width: 80px;
    background-color: transparent;
    margin: auto .5rem;
    font-weight: bolder;
    font-size: .8rem;
    &:hover{
      background-color: rgba($color: #fff, $alpha: .3);
      transition: all .25s ease-in-out;
      cursor: pointer;
    }
  }
}
//SIGNUP
div.signup{
  max-width: 40ch;
  margin:2rem auto;
  text-align: center;
  padding: 2rem;
  background-color: rgba(255, 255, 255, .1);
  border-radius: 20px;
  form{
    display:flex;
    flex-direction: column;
    width:25ch;
    margin:0 auto;
    input{
      margin:.5rem 0;
      padding: .25rem;
      border-radius: 10px;
      border:none;
      text-align: center;
      background-color: rgba(255, 255, 255, .75);
    }
    input[type="submit"]{
      min-width: 15ch;
      margin: 0 auto;
      margin-top: .5rem;
      background-color: #e06219;
      color:#fff;
      font-weight: bolder;
      border: 1px solid #fff;
      &:hover{
        background-color: #fff;
        color: #e06219;
        cursor: pointer;
      }
    }
    div.submitCancel{
      display:flex;
      justify-content: space-between;
      button{
        width:95%;
        border: 1px solid #fff;
        border-radius: 15px;
        padding: .25rem 0;
        margin-top: .5rem;
        font-weight: bolder;
        transition: all 0.25s ease-in-out;
        &:nth-of-type(1){
          margin-right: .25rem;
          background-color: transparent;
          color:#fff;
          &:hover{
            background-color: #fff;
            color: #e06219;
            cursor: pointer;
          }
        }
        &:nth-of-type(2){
          margin-left: .25rem;
          background-color: rgba(255, 255, 255, .25);
          color:#fff;
          &:hover{
            background-color: #e06219;
            color: #fff;
            cursor: pointer;
          }
        }
      }
    }
  }
}
// MAIN
div.main{
  display:grid;
  grid-template-columns: 55px 9fr;
  background: linear-gradient(45deg, rgb(204, 37, 14) 0%,  rgb(214, 99, 5) 50%, rgb(100, 30, 2) 100%);
  background-position: 100% 100%;
  background-repeat: no-repeat;
  nav.mainNav{
    height: 100vh;
    margin: 0;
    padding: .5rem;
    display:flex;
    flex-direction: column;
    background-color: rgba(0, 0, 0, .5);
    a{
      width:40px;
      height:40px;
      margin:.5rem 0;
      img{
        width: 40px;
        height: 40px;
        filter:opacity(.5)
      }
      img:hover{
        cursor: pointer;
        filter:opacity(1);
        transition: all .25s ease-in-out;
      }
      img.select{
        filter:opacity(1);
        transition: all .25s ease-in-out;
      }
    }
  }
}

// MAIN > PROFILE
div.profile{
  margin: 2rem;
  h3{
    margin-top: 0;
    text-decoration: underline;
  }
  ul{
    padding:0;
    li{
      list-style-type: none;
      margin: 1rem 0;
    }
  }
}
// MAIN > USERLIST
div.userSpace{
  position: relative;
  display: grid;
  grid-template-columns: 200px 1fr;
  ul.userlist{
    padding:0;
    margin: 0;
    background-color: rgba(0, 0, 0, .2);
    li{
      list-style-type: none;
      padding: .5rem;
      border-bottom: 1px solid rgba(255, 255, 255, .75);
      span{
        display:block;
      }
      span.name{
        font-size: .95rem;
        margin-bottom: .15rem;
      }
      span.email{
        font-size: .75rem;
        font-style: italic;
      }
    }
  }
}
div.chatWindowWrapper{
  margin: 5rem;
  div.chatWindow{
    height:600px;
    max-width: 600px;
    margin: 0 auto ;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background-color: rgba(255, 255, 255, 0.2);
    border-radius: 20px;
    ul{
      padding: 0 1rem;
      li{
        list-style-type: none;
        display:flex;
        flex-direction: column;
        padding:.5rem;
        border-radius: 5px;
        background-color: rgba(255, 255, 255, .25);
        width:fit-content;
        margin: .5rem 0;
        color:#fff;
        div.nameTime{
          min-width: 150px;
          display: flex;
          justify-content: space-between;
          font-size: .85rem;
          color:#000;
          margin-bottom: .25rem;
          span.chatName{
            color:#000;
            font-weight: 600;
            font-style: italic;
          }
        }
      }
      li.you{
        margin-inline-start:auto;
        background-color: rgba(224, 92, 52, 0.75);
      }
      li.other{
        margin-inline-start:inherit;
        background-color: rgba(255, 255, 255, 0.15);
      }
    }
    div.field{
      margin: 1rem 1rem;
      display:flex;
      justify-content: space-between;
      height:2rem;
      input{
        width:100%;
        margin-right: 1rem;
        background-color: rgba(255, 255, 255, .15);
        border:none;
        border-bottom: 1px solid rgba(255, 255, 255, .5);
        &:hover{
          border:none;
          background-color: rgba(255, 255, 255, .25);
          border-bottom: 1px solid rgba(255, 255, 255, .75);
        }
      }
      button{
        background-color: transparent;
        border: 1px solid #fff;
        border-radius: 10px;
        padding:1rem auto;
        align-items: center;
        color:#fff;
      }
    }
  }
}
.msgContainer{
  width:100%;
  height: 100%;
  overflow-y: scroll;
  overflow-x: hidden;
  &::-webkit-scrollbar{
    display:none;
  }
}
// MAIN > SETTING
div.setting{
  margin:2rem;
  button{
    border: 1px solid #fff;
    border-radius: 20px;
    padding: .25rem .5rem;
    color: #fff;
    min-width: 80px;
    background-color: transparent;
    font-weight: bolder;
    font-size: .8rem;
    &:hover{
      background-color: rgba($color: #fff, $alpha: .3);
      transition: all .25s ease-in-out;
      cursor: pointer;
    }
  }
}